import { PageContainer } from '../components';
import PageTitle from '../components/PageTitle';
import { Box, Button, Stack, Typography } from '@mui/material';
import { ArrowForward } from '@mui/icons-material';
import { useEffect, useState } from 'react';

const Error = () => {
  const [infoText, setInfoText] = useState('');

  const pollIokInfo = async () => {
    const res = await fetch(
      'https://wy8qg2hpoh.execute-api.eu-west-1.amazonaws.com/default/iokRegistrationData?info=true',
    );
    const text = await res.text();
    setInfoText(text);
  };

  useEffect(() => {
    pollIokInfo();

    const interval = setInterval(pollIokInfo, 10000);
    return () => clearInterval(interval);
  }, []);

  return (
    <PageContainer container>
      <PageTitle>Technikai hiba lpett fel </PageTitle>
      <Stack>
        <Typography>
          Sajnos valami gond van, ezrt nem jnnek az adatok a virtulis konferenciakzpontunk
          adatbzisbl. Mindent megtesznk, hogy minl hamarabb helyrelljon a rendszer. Ha ez nem
          trtnne meg egy-kt percen bell, akkor krlek, ltogass el a HTTP Alptvny honlapjn
          tallhat IOK 2025 informcis oldalra, ahol rszlesebb informcival fogunk szolglni.
          {!!infoText && (
            <>
              <br />
              <br />
              {infoText}
            </>
          )}
        </Typography>
        <Box>
          <Button
            variant="contained"
            color="secondary"
            startIcon={<ArrowForward />}
            sx={{ pt: 1, pb: 1, mt: 2 }}
            href="https://http-alapitvany.hu/iok-info"
          >
            Tovbb az IOK 2025 inf oldalra a HTTP Alaptvny honlapjn
          </Button>
        </Box>
      </Stack>
    </PageContainer>
  );
};

export default Error;
